import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '@/views/Index'
import Project from '@/views/Project'

Vue.use(VueRouter)

const routes = [
    {
        name:'index',
        path:'/index',
        component:Index
    },
    {
        name:'project',
        path:'/project',
        component:Project
    },
    {
        path:'/',
        component:Index
    }
]

const router = new VueRouter({
    routes,
    mode: 'history',
})

export default router
