import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state:{
        count:0
    },
    getters:{
        getCount(state){
            return state.count
        }
    },
    mutations:{
        increment(state, val){
            state.count = state.count + val
        }
    },
    actions:{
        increment(context, val){
            context.commit('increment', val)
        }
    }
})

export default store